<template>
    <h1>一个控制器</h1>

    <label for="ipAddress">IP地址:</label>
    <input type="text" id="ipAddress" v-model="ipAddress"><br><br>

    <label for="deviceId">设备ID:</label>
    <input type="text" id="deviceId" v-model="deviceId"><br><br>

    <label for="controllerName">控制器名:</label>
    <input type="text" id="controllerName" v-model="controllerName"><br><br>
    <button @click=handleClick1>设为当前控制器</button>
    <button @click=handleClick2>打开工作目录</button>
</template>

<script>


export default {
    components: {
    },
    data() {
        return {
            ipAddress: "192.168.20.145",
            deviceId: '9',
            controllerName: "Line1-0.9",



            
        };
    },
    props: {
        msg: String,
    },
    methods: {
        handleClick1() {
            // 在这里可以执行你想要的操作，例如发送请求或处理数据
            if (window.electronAPI) {
                window.electronAPI.setCurrentController({
                    ipAddress: this.ipAddress,
                    deviceId: this.deviceId,
                    controllerName: this.controllerName
                })
                // 示例：在控制台打印输入的值
            }
        },

        handleClick2() {
            if (window.electronAPI) {
                // 在这里可以执行你想要的操作，例如发送请求或处理数据
                window.electronAPI.openWorkingDisk()

            }
        }
    },
    computed: {

    },
    mounted() {

    },
    created() {
        let c = {
            ip: "192.168.20.145",
            device_id: 9,
            device_name: "Line1-0.9"
        }
        if (window.electronAPI) {
            c = window.electronAPI.getController();
        }

        this.ipAddress = c.ip;
        this.deviceId = c.device_id;
        this.controllerName = c.device_name;
    },
    watch: {

    }
}

</script>

